:root {
    --color-text-01: rgba(255, 255, 255, 1);
    --color-text-02: rgba(188, 194, 204, 1);
    --color-text-03: rgba(126, 133, 143, 1);
    --color-text-icons-contrast: rgba(31, 31, 31, 1);
    --color-bg-01: rgba(31, 31, 31, 1);
    --color-bg-02: rgba(38, 38, 38, 1);
    --color-bg-03: rgba(46, 46, 46, 1);
    --color-borders-01: rgba(61, 61, 61, 1);
    --color-borders-02: rgba(71, 71, 71, 1);
    --color-bg-contrast: rgba(255, 255, 255, 1);
    --color-red-01: rgba(228, 88, 71, 1);
}

html {
    font-family: "Inter", Helvetica, Arial, sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 15px;
    line-height: 150%;
    background: #fff;
    color: #fff;
    letter-spacing: normal;
    text-size-adjust: none;
    -webkit-text-size-adjust: none;
    -moz-text-size-adjust: none;
    -ms-text-size-adjust: none;
    height: -webkit-fill-available;

    background-color: var(--color-bg-01);

    @media screen and (min-width: $w1376) {
        font-size: 16px;
    }

}



html,
body {
    scrollbar-color: var(--black) #fff;
    scrollbar-width: thin;
    scrollbar-gutter: stable;


    @media screen and (min-width: $w992) {
        &::-webkit-scrollbar {
            width: 4px;
            border-radius: 4px;
        }

        &::-webkit-scrollbar-track {
            background-color: #fff;
            border-radius: 0;
        }

        &::-webkit-scrollbar-thumb {
            background-color: var(--black);
            border-radius: 0;
        }
    }
}

html * {
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
}

body {
    min-height: 100vh;
    min-height: -webkit-fill-available;

    & * {
        @include scrollbar();
    }

}




button:active,
button:hover,
input:active,
input:focus,
input:hover {
    outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul {
    margin: 0;
    padding: 0;
    line-height: 130%;
}

body.page-hidden {
    touch-action: none;
    overflow: hidden !important;
    overscroll-behavior-y: none;
}

* {
    box-sizing: border-box;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a {
    color: #fff;
    text-decoration: none;
}

img {
    display: block;
    max-width: 100%;
}

ul {
    list-style-type: none;
}


div.hidden {
    display: none;
}

.hide {
    display: none;
}

section {
    overflow: hidden;
    position: relative;
}

main {
    width: 100%;
    min-height: 70vh;
}




input[type="date"]::-webkit-inner-spin-button,
input[type="date"]::-webkit-calendar-picker-indicator {
    opacity: 0.5;
    background-color: transparent;
    opacity: 0;
    position: absolute;
    left: 0;
    width: 95%;
    cursor: pointer;
}

input[type="date"] {
    position: relative;
}

@mixin container-breakpoint {
    padding: 0 24px;
    min-width: 316px;

    @media screen and (min-width: $w480) {
        padding: 0 24px;
        max-width: 100%;
    }

    @media screen and (min-width: $w576) {
        padding: 0 24px;
        max-width: 100%;
    }

    @media screen and (min-width: $w760) {
        padding: 0 45px;
        max-width: 100%;
    }

    @media screen and (min-width: $w992) {
        padding: 0 45px;
        max-width: 100%;
    }

    @media screen and (min-width: $w1200) {
        padding: 0 45px;
        max-width: 100%;
    }

    @media screen and (min-width: $w1440) {
        padding: 0 45px;
        max-width: 1890px;
    }
}

.container {
    width: 100%;
    margin: 0 auto;
    box-sizing: border-box;
    @include container-breakpoint;
}



.table-responsive {
    overflow-x: auto;

    & table {
        min-width: auto;
    }
}


