/* =========================================== */





.lazyload {
    opacity: 0;
}

.lazyloaded {
    opacity: 1;
    transition: opacity 0.3s ease;
}

.overflow--hidden {
    overflow: hidden !important;
}

.overflow--hidden-fixed {
    overflow: hidden !important;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}

.overflow--hidden-scrollBar {
    overflow-x: hidden !important;
    overflow-y: scroll !important;
}

.safari-fixed {
    height: calc(100vh - 1px);
    overflow: hidden !important;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}

.formated-text {

    & * {
        line-height: 140%;
    }

    & h1,
    & h2,
    & h3,
    & h4,
    & h5,
    & h6 {
        margin-bottom: 25px;
        line-height: 120%;
        color: #222429;
        font-weight: 700;
    }

    @media screen and (min-width: $w1200) {
        & h1 {
            font-size: 46px;
        }

        & h2 {
            font-size: 38px;
        }

        & h3 {
            font-size: 32px;
            color: #222429;
        }

        & h4 {
            font-size: 28px;
            color: #222429;
        }

        & h5 {
            font-size: 24px;
            color: #222429;

        }

        & h6 {
            font-size: 18px;
        }
    }

    & .table {
        overflow-x: auto;
    }

    & table {

        border: 1px solid #DDDDDD;
        border-collapse: collapse;
        margin: 35px 0;
        width: 100%;
        min-width: 600px;

        & th {
            padding: 13px 25px;
            background-color: #DFF0FD;
            color: #000;
            text-decoration: transparent;
            font-weight: bold;
            text-align: left;
            border: 1px solid #CACACA;
            border-bottom-color: transparent;

            &:first-child {
                text-align: left;
            }

            @media screen and (min-width: $w760) {
                padding: 15px 25px;

            }


        }

        & td {
            padding: 10px 15px;
            border: 1px solid #CACACA;
            font-weight: normal;
            text-align: left;
            min-width: 150px;

            &:first-child {
                text-align: left;
                font-weight: bold;
            }

            @media screen and (min-width: $w760) {
                padding: 10px 25px;

            }

            & strong {
                display: block;
                margin-bottom: 10px;
            }

            &:first-child {
                font-weight: normal;
            }
        }

    }

    & a {
        color: $accent-2-1;
        //text-decoration: underline;
    }

    & div:not([class]),
    & p {
        margin: 10px 0;
        line-height: 150%;

        @media screen and (min-width: $w1440) {
            margin: 20px 0;
        }


    }

    & ul {

        margin: 20px 0;

        & li {

            position: relative;
            padding-left: 30px;
            padding-bottom: 10px;



            &:last-child {
                padding-bottom: 0;
            }

            &::before {
                content: '';
                width: 20px;
                height: 20px;
                display: block;
                position: absolute;
                left: -0;
                top: 0.15em;
                border-radius: 50%;
                // background-color: #5FB3F6;

                background-image: url(data:image/svg+xml;base64,);
            }
        }
    }

    & img {

        margin: 35px 0;
    }

    & ol {
        counter-reset: number-list 00;
        padding: 20px 0;

        &>li {

            counter-increment: number-list 1;
            list-style-type: none;
            position: relative;
            margin-bottom: 10px;
            color: #000;
            padding-left: 20px;

            @media screen and (min-width: $w760) {

                padding-left: 30px;
            }



            &::before {
                content: counter(number-list);
                color: #5FB3F6;
                font-weight: bold;
                min-width: 35px;
                display: inline-block;
                position: absolute;
                left: 0;

                @media screen and (min-width: $w760) {}
            }
        }
    }

    & strong {
        font-weight: 700;
    }

    & .note {
        background: var(--bg-01);
        padding: 16px;
        border-radius: 8px;
        margin: 1.5em 0;

        & strong {
            color: $main;
            font-weight: 600;

            display: block;
        }

        & p {
            margin: 0.7em 0;
        }
    }

    & blockquote {
        border: none;
        padding: 16px;
        font-size: 14px;
        background-color: #EFF8FF;
        line-height: 147%;

        @media screen and (min-width: $w760) {
            font-size: 16px;
            padding: 20px 24px;
        }
    }
}

.table-responsive {
    overflow-x: auto;

    & table {
        min-width: auto;
    }
}

.lazyload {
    opacity: 0;
}

.lazyloaded {
    opacity: 1;
    transition: opacity 0.5s ease;
}